
import { Component, Fragment, useState } from 'react';
import { createPortal } from 'react-dom';
import { Link, NavLink } from "react-router-dom";
import '../styles/header.css';
import Menu from '@mui/icons-material/Menu';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import Button from '@mui/material/Button';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import ListItemButton from '@mui/material/ListItemButton';

const Header = (props) => {
    const [state, setState] = useState({
        top: false,
        left: false,
        bottom: false,
        right: false,
      });
    
      const toggleDrawer = (anchor, open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
          return;
        }
    
        setState({ ...state, [anchor]: open });
      };
    
      const list = (anchor) => (
        <Box
          sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 250 }}
          role="presentation"
          onClick={toggleDrawer(anchor, false)}
          onKeyDown={toggleDrawer(anchor, false)}
        >
          <div className="col-2 logo-site">
            <Link to="/" ><img alt="logo OracleBet" src="/polkadot/logo.png"/></Link>
          </div>
          <Divider/>
          <List>
            {[
                {
                    label: 'Wallet',
                    to: '/wallet'
                },
                {
                    label: 'Derivatives',
                    to: '/derivatives'
                },
                {
                    label: 'Community',
                    to: '/community'
                },
                {
                    label: 'Support',
                    to: '/support'
                },
            ].map((m, index) => (
              <ListItem button key={m.label}>
                <ListItemButton component="a" href={m.to}>
                <ListItemText style={{
                    color: ' #80BF75', 
                    textTransform: 'capitalize',
                    fontWeight: '500',
                    fontSize: '1rem'}} 
                
                    primary={m.label} />
                </ListItemButton>
                
              </ListItem>
            ))}
          </List>
        </Box>
      );
    
    return createPortal(
        <Fragment>
            <div className="app-nav-pc bg-white position-fixed w-100 top-0">
                <div className="row p-0 m-0">
                    <div className="col-2 logo-site">
                        <Link to="/" ><img alt="logo OracleBet" src="/polkadot/logo.png"/></Link>
                    </div>
                    <div className="col-6">
                        <div className="row p-0 justify-content-center">
                            <nav className="pp-menu">
                                <ul className="pp-wrap-menu-items">
                                    <li className="pp-menu-item text-indigo"><Link to="/wallet">Wallet</Link></li>
                                    <li className="pp-menu-item text-indigo"><Link to="/derivatives">Derivatives</Link></li>
                                    <li className="pp-menu-item text-indigo"><Link to="/community">Community</Link></li>
                                    <li className="pp-menu-item text-indigo"><Link to="/support">Support</Link></li>
                                </ul>
                            </nav>
                        </div>
                    </div>
                    <div className="col-4">
                        <div className="row menu-buttons justify-content-center p-0 mr-0">

                            <div className="col-auto p-0"><Link className="button" to="/login">Login</Link></div>  
                            <div className="col-auto p-0"><Link className="button" to="/register">Register</Link></div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="app-nav-mobile bg-white position-fixed w-100 top-0">
                <div className="row p-0 m-0">
                    <div className="col-2 logo-site">
                        <Menu style={{color: '#80BF75'}} onClick={toggleDrawer('left', true)}></Menu>
                        <Drawer
                            anchor='left'
                            open={state['left']}
                            onClose={toggleDrawer('left', false)}
                        >
                            {list('left')}
                        </Drawer>
                    </div>
                    <div className="col-2">
                        
                    </div>
                    <div className="col-8">
                        <div className="row menu-buttons justify-content-center p-0 mr-0">

                            <div className="col-auto p-0"><Link className="button" to="/login">Login</Link></div>  
                            <div className="col-auto p-0"><Link className="button" to="/register">Register</Link></div>
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>, 
        document.getElementById("app-header")
    )
    
}

export default Header;